/**
*  这个文件是在less文件内部@import导入使用的,
*  类名如果在页面标签上直接调用，由于多次引用，就会生成重复的类名，
*  所以这个地方的类名或者变量“只能”在less文件里面调用
*/
/*小块内部间距，默认15px*/
.white-bgcolor {
  background-color: #fff;
}
.stu-overstep-hidden {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.middle-center {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
}
.white-space {
  padding: 20px;
  background-color: #fff;
}
.stu-components-box .margin-top-20 {
  margin-top: 20px;
}
.stu-components-box .left-box {
  float: left;
}
.stu-components-box .right-box {
  float: right;
}
.stu-components-box > .left-box {
  width: 880px;
}
.stu-components-box > .left-box .top-left {
  padding: 20px;
  background-color: #fff;
}
.stu-components-box > .left-box .bottom-left {
  padding: 20px;
  background-color: #fff;
  margin-top: 20px;
}
.stu-components-box > .right-box {
  width: 300px;
}
.stu-components-box > .right-box .top-right {
  padding: 20px;
  background-color: #fff;
}
.stu-components-box > .right-box .content-right {
  margin-top: 20px;
  padding: 20px;
  background-color: #fff;
}
.stu-components-box > .right-box .bottom-right {
  margin-top: 20px;
  padding: 20px;
  background-color: #fff;
}
/**
* 这个文件是在main.js里面引用的，试用于整个项目
* 其他页面不需要再引入了，在html标签上直接写在class上使用就行
*/
/*初始化或重置一些元素样式 begin*/
body {
  color: #575757;
  font-family: "Microsoft Yahei";
  background-color: #f1f1f1;
  font-size: 14px;
  min-width: 1200px;
}
* {
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: #333;
}
img {
  vertical-align: middle;
}
/*初始化或重置一些元素样式 end*/
.m-t-10 {
  margin-top: 10px;
}
.m-t-15 {
  margin-top: 15px;
}
.m-t-20 {
  margin-top: 20px;
}
.f-l {
  float: left;
}
.f-r {
  float: right;
}
.stuCorAct {
  color: #00ad8b !important;
}
.stuBackAct {
  background-color: #00ad8b !important;
}
.stu-container {
  width: 1200px;
  margin: 0 auto;
}
.stu-container-padding {
  padding: 25px 0;
  flex: 1;
}
.stu-after-clear:after {
  content: '';
  display: block;
  clear: both;
}
.stu-parent-clear {
  overflow: hidden;
  zoom: 1;
}
.two-columns:after {
  content: '';
  display: block;
  clear: both;
}
.no-pointer {
  cursor: text;
}
.pointer {
  cursor: pointer;
}
.stu-red {
  color: #ff471c;
}
.padding-l-r {
  padding: 0 5px;
}
/**按钮公用部分* end*/
.stu-green-btn {
  border-color: #00ad8b;
  color: #00ad8b;
}
.stu-green-btn:hover {
  color: #fff;
  background-color: #00ad8b !important;
}
.stu-green-btn:focus {
  color: #fff;
  background-color: #008f73 !important;
}
.stu-gray-btn {
  border-color: #ccc !important;
  color: #999;
}
.stu-gray-btn:hover {
  color: #fff;
  background-color: #ccc !important;
}
.stu-gray-btn:focus {
  color: #fff;
  background-color: #b1b1b1 !important;
}
.stu-orange-btn {
  border-color: #ff471c;
  color: #ff471c;
}
.stu-orange-btn:hover {
  color: #fff;
  background-color: #ff471c !important;
}
.stu-orange-btn:focus {
  color: #fff;
  background-color: #e7431d !important;
}
/**按钮公用部分* end*/
.stu-list-item > li {
  padding: 20px 0;
  border-bottom: 1px solid #ecf0f5;
}



