/**
*  这个文件是在less文件内部@import导入使用的,
*  类名如果在页面标签上直接调用，由于多次引用，就会生成重复的类名，
*  所以这个地方的类名或者变量“只能”在less文件里面调用
*/
/*小块内部间距，默认15px*/
.white-bgcolor {
  background-color: #fff;
}
.stu-overstep-hidden {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.middle-center {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
}
.white-space {
  padding: 20px;
  background-color: #fff;
}
.stu-components-box .margin-top-20 {
  margin-top: 20px;
}
.stu-components-box .left-box {
  float: left;
}
.stu-components-box .right-box {
  float: right;
}
.stu-components-box > .left-box {
  width: 880px;
}
.stu-components-box > .left-box .top-left {
  padding: 20px;
  background-color: #fff;
}
.stu-components-box > .left-box .bottom-left {
  padding: 20px;
  background-color: #fff;
  margin-top: 20px;
}
.stu-components-box > .right-box {
  width: 300px;
}
.stu-components-box > .right-box .top-right {
  padding: 20px;
  background-color: #fff;
}
.stu-components-box > .right-box .content-right {
  margin-top: 20px;
  padding: 20px;
  background-color: #fff;
}
.stu-components-box > .right-box .bottom-right {
  margin-top: 20px;
  padding: 20px;
  background-color: #fff;
}
.stu-footer {
  padding-top: 35px;
  font-size: 12px;
  color: #fff;
  width: 100%;
  height: 102px;
  background-color: #000;
  text-align: center;
}
.stu-footer .footer-list > li {
  display: inline-block;
  padding: 0 5px;
}
.stu-footer .footer-list > .line:after {
  content: '|';
  padding-left: 10px;
}
.stu-footer .license {
  margin-top: 10px;
  color: #575757;
}



