/*小块内部间距，默认15px*/
.white-bgcolor {
  background-color: #fff;
}
.stu-overstep-hidden {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.middle-center {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
}
.white-space {
  padding: 20px;
  background-color: #fff;
}
.stu-components-box .margin-top-20 {
  margin-top: 20px;
}
.stu-components-box .left-box {
  float: left;
}
.stu-components-box .right-box {
  float: right;
}
.stu-components-box > .left-box {
  width: 880px;
}
.stu-components-box > .left-box .top-left {
  padding: 20px;
  background-color: #fff;
}
.stu-components-box > .left-box .bottom-left {
  padding: 20px;
  background-color: #fff;
  margin-top: 20px;
}
.stu-components-box > .right-box {
  width: 300px;
}
.stu-components-box > .right-box .top-right {
  padding: 20px;
  background-color: #fff;
}
.stu-components-box > .right-box .content-right {
  margin-top: 20px;
  padding: 20px;
  background-color: #fff;
}
.stu-components-box > .right-box .bottom-right {
  margin-top: 20px;
  padding: 20px;
  background-color: #fff;
}
.stu-hp-sidebar .nav-ul,
.stu-hp-sidebar .right-con {
  padding: 20px;
  background-color: #fff;
}
.stu-hp-sidebar .left-nav {
  float: left;
  width: 280px;
}
.stu-hp-sidebar .right-con {
  float: right;
  width: 900px;
}
.stu-hp-sidebar .photo-wrap {
  height: 140px;
  padding: 20px;
  background-image: url('../../../assets/images/person-bg.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}
.stu-hp-sidebar .photo-wrap .photo-img {
  width: 100%;
  -moz-border-radius: 5px;
  /* Firefox */
  -webkit-border-radius: 5px;
  /* Safari 和 Chrome */
  border-radius: 5px;
  /* Opera 10.5+, 以及使用了IE-CSS3的IE浏览器 */
}
.stu-hp-sidebar .photo-info-wrap {
  padding: 12px 15px;
  padding-right: 0;
  font-size: 12px;
}
.stu-hp-sidebar .photo-info-wrap > h4 {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 15px;
}
.stu-hp-sidebar .photo-info-wrap > .desc {
  margin-top: 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.stu-hp-sidebar .nav-ul {
  font-size: 14px;
}
.stu-hp-sidebar .nav-ul .router-link-active {
  background: #f4f7f5;
  color: #00ad8b;
  font-weight: bold;
}
.stu-hp-sidebar .nav-ul .link {
  display: block;
  height: 42px;
  padding-left: 10px;
  line-height: 42px;
}
.stu-hp-sidebar .nav-ul .link:hover {
  background: #f4f7f5;
  color: #00ad8b;
  font-weight: bold;
}
.stu-hp-sidebar .nav-ul .iconfont {
  padding-right: 8px;
  font-size: 14px;
}



