/**
*  这个文件是在less文件内部@import导入使用的,
*  类名如果在页面标签上直接调用，由于多次引用，就会生成重复的类名，
*  所以这个地方的类名或者变量“只能”在less文件里面调用
*/
/*小块内部间距，默认15px*/
.white-bgcolor {
  background-color: #fff;
}
.stu-overstep-hidden {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.middle-center {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
}
.white-space {
  padding: 20px;
  background-color: #fff;
}
.stu-components-box .margin-top-20 {
  margin-top: 20px;
}
.stu-components-box .left-box {
  float: left;
}
.stu-components-box .right-box {
  float: right;
}
.stu-components-box > .left-box {
  width: 880px;
}
.stu-components-box > .left-box .top-left {
  padding: 20px;
  background-color: #fff;
}
.stu-components-box > .left-box .bottom-left {
  padding: 20px;
  background-color: #fff;
  margin-top: 20px;
}
.stu-components-box > .right-box {
  width: 300px;
}
.stu-components-box > .right-box .top-right {
  padding: 20px;
  background-color: #fff;
}
.stu-components-box > .right-box .content-right {
  margin-top: 20px;
  padding: 20px;
  background-color: #fff;
}
.stu-components-box > .right-box .bottom-right {
  margin-top: 20px;
  padding: 20px;
  background-color: #fff;
}
.stu-login .warning {
  line-height: 36px;
  font-size: 14px;
  text-align: center;
}
.stu-login .warning .bowerSupport {
  display: none;
}
.stu-login .warning .login-warning {
  color: red;
}
.stu-login .banner {
  background-image: url('../../../assets/images/banner.png');
  background-repeat: no-repeat;
  background-position: '';
  background-size: 100% 430px;
  height: 430px;
  position: relative;
}
.stu-login .login-box {
  position: absolute;
  top: 20px;
  left: 70%;
  width: 347px;
  height: 338px;
  background: #fff;
  padding: 0 20px;
  font-family: -webkit-pictograph;
}
.stu-login .login-box .title {
  margin: 23px 0 20px;
  font-weight: bold;
  font-size: 18px;
}
.stu-login .login-box .errorInfo {
  color: red;
}
.stu-login .login-box .input-group {
  margin-top: 25px;
  width: 100%;
  height: 38px;
  -moz-border-radius: 6px;
  /* Firefox */
  -webkit-border-radius: 6px;
  /* Safari 和 Chrome */
  border-radius: 6px;
  /* Opera 10.5+, 以及使用了IE-CSS3的IE浏览器 */
  border: 1px #c0c0c0 solid;
}
.stu-login .login-box .input-group-label {
  display: inline-block;
  height: 100%;
  line-height: 40px;
  width: 40px;
  text-align: center;
  cursor: pointer;
}
.stu-login .login-box .input-group-label i {
  font-size: 20px;
  color: gainsboro;
}
.stu-login .login-box .input-user {
  border-right: 1px #c0c0c0 solid;
}
.stu-login .login-box .form-field {
  position: relative;
  top: -3px;
  width: 216px;
  height: 30px;
  border: none;
}
.stu-login .login-box .form-field:focus {
  outline: none;
}
.stu-login .login-box .loginBtn {
  background: #ff474c;
  border: none;
  -moz-border-radius: 6px;
  /* Firefox */
  -webkit-border-radius: 6px;
  /* Safari 和 Chrome */
  border-radius: 6px;
  /* Opera 10.5+, 以及使用了IE-CSS3的IE浏览器 */
  height: 40px;
  line-height: 40px;
  width: 100%;
  text-align: center;
  margin-top: 25px;
  color: #fff;
  font-size: 24px;
  font-weight: normal;
  cursor: pointer;
}
.stu-login .foot {
  text-align: center;
  margin-top: 10px;
}



