  width: 42px;
  animation: rotate 2s linear infinite;
}
.el-loading-spinner .path {
  animation: dash 1.5s ease-in-out infinite;
  stroke-dasharray: 1, 100;
  stroke-dashoffset: 0;
  stroke-width: 2;
  stroke: #00ad8b;
  stroke-linecap: round;
}
