  0% {
    stroke-dasharray: 1, 100;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 45, 100;
    stroke-dashoffset: -35px;
  }
  100% {
    stroke-dasharray: 45, 100;
    stroke-dashoffset: -124px;
  }
}
