.el-tabs {
  display: inline-block;
  width: 100%;
}
.el-tabs__header {
  border-bottom: 1px solid #ecf0f5;
  padding: 0;
  position: relative;
  margin: 0 0 15px;
}
.el-tabs__active-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #00ad8b;
  z-index: 1;
  transition: transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  list-style: none;
}
.el-tabs__item {
  padding: 0 16px;
  height: 42px;
  box-sizing: border-box;
  line-height: 42px;
  float: left;
  list-style: none;
  font-size: 14px;
  color: #575757;
  margin-bottom: -1px;
  position: relative;
}
.el-tabs__item .el-icon-close {
  border-radius: 50%;
  text-align: center;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  margin-left: 5px;
}
.el-tabs__item .el-icon-close:before {
  transform: scale(0.7, 0.7);
  display: inline-block;
}
.el-tabs__item .el-icon-close:hover {
  background-color: #99a9bf;
  color: #fff;
}
.el-tabs__item:hover {
  color: #575757;
  cursor: pointer;
}
.el-tabs__item.is-disabled {
  color: #bbb;
  cursor: default;
}
.el-tabs__item.is-active {
  color: #00ad8b;
}
.el-tabs__content {
  overflow: hidden;
  position: relative;
}
.el-tabs--card .el-tabs__active-bar {
  display: none;
}
.el-tabs--card .el-tabs__item {
  border: 1px solid transparent;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.el-tabs--card .el-tabs__item.is-closable:hover {
  padding-right: 9px;
  padding-left: 9px;
}
.el-tabs--card .el-tabs__item.is-closable:hover .el-icon-close {
  width: 14px;
}
.el-tabs--card .el-tabs__item.is-active {
  border: 1px solid #d3dce6;
  border-bottom-color: #fff;
  border-radius: 4px 4px 0 0;
}
.el-tabs--card .el-tabs__item.is-active.is-closable {
  padding-right: 16px;
  padding-left: 16px;
}
.el-tabs--card .el-tabs__item.is-active.is-closable .el-icon-close {
  width: 14px;
}
.el-tabs--card .el-tabs__item .el-icon-close {
  position: relative;
  font-size: 12px;
  width: 0;
  height: 14px;
  vertical-align: middle;
  line-height: 15px;
  overflow: hidden;
  top: -1px;
  right: -2px;
  transform-origin: 100% 50%;
}
.el-tabs--border-card {
  background: #fff;
  border: 1px solid #d3dce6;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12), 0px 0px 6px 0px rgba(0, 0, 0, 0.04);
}
.el-tabs--border-card .el-tabs__content {
  padding: 15px;
}
.el-tabs--border-card .el-tabs__header {
  background-color: #eff2f7;
  margin: 0;
}
.el-tabs--border-card .el-tabs__item {
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  border: 1px solid transparent;
  border-top: 0;
  margin-right: -1px;
  margin-left: -1px;
}
.el-tabs--border-card .el-tabs__item.is-active {
  background-color: #fff;
  border-right-color: #d3dce6;
  border-left-color: #d3dce6;
}
.el-tabs--border-card .el-tabs__item.is-active:first-child {
  border-left-color: #d3dce6;
}
.el-tabs--border-card .el-tabs__item.is-active:last-child {
  border-right-color: #d3dce6;
}
.el-tab-pane {
  width: 100%;
  display: inline-block;
