.el-step {
  position: relative;
  vertical-align: top;
}
.el-step.is-vertical .el-step__head,
.el-step.is-vertical .el-step__main {
  display: inline-block;
}
.el-step.is-vertical .el-step__main {
  padding-left: 10px;
}
.el-step.is-horizontal {
  display: inline-block;
}
.el-step__line {
  display: inline-block;
  position: absolute;
  border-color: inherit;
  background-color: #c0ccda;
}
.el-step__line.is-vertical {
  width: 2px;
  box-sizing: border-box;
  top: 32px;
  bottom: 0;
  left: 15px;
}
.el-step__line.is-horizontal {
  top: 15px;
  height: 2px;
  left: 32px;
  right: 0;
}
.el-step__line.is-icon.is-horizontal {
  right: 4px;
}
.el-step__line-inner {
  display: block;
  border-width: 1px;
  border-style: solid;
  border-color: inherit;
  transition: all 150ms;
  width: 0;
  height: 0;
}
.el-step__icon {
  display: block;
  line-height: 28px;
}
.el-step__icon > * {
  line-height: inherit;
  vertical-align: middle;
}
